import React from 'react'
import PropTypes from 'prop-types'

import { H1, H2, H3, H4, P1, P2, P3, Lbl, Lgd } from './styles'

const Text = ({
  heading,
  paragraph,
  label,
  legend,
  children,
  invert,
  fade,
  color,
  bold,
  htmlFor,
  dataTestId,
  id,
  zeroMargin,
  design,
  as
}) => {
  const commonProps = {
    invert,
    fade,
    color,
    'data-test-id': dataTestId,
    zeroMargin,
    design,
    id,
    as
  }

  if (heading) {
    switch (heading) {
      case '1':
        return <H1 {...commonProps}>{children}</H1>
      case '2':
        return <H2 {...commonProps}>{children}</H2>
      case '3':
        return <H3 {...commonProps}>{children}</H3>
      case '4':
        return <H4 {...commonProps}>{children}</H4>
      default:
        return <H1 {...commonProps}>{children}</H1>
    }
  }
  if (paragraph) {
    switch (paragraph) {
      case '1':
        return (
          <P1 {...commonProps} {...{ bold }}>
            {children}
          </P1>
        )
      case '2':
        return (
          <P2 {...commonProps} {...{ bold }}>
            {children}
          </P2>
        )
      case '3':
        return (
          <P3 {...commonProps} {...{ bold }}>
            {children}
          </P3>
        )
      default:
        return (
          <P2 {...commonProps} {...{ bold }}>
            {children}
          </P2>
        )
    }
  }
  if (label) {
    return (
      <Lbl
        large={label === 'large'}
        design={design}
        data-test-id={dataTestId}
        id={id}
        as={as}
        {...{ htmlFor, color }}
      >
        {children}
      </Lbl>
    )
  }
  if (legend) {
    return (
      <Lgd
        large={legend === 'large'}
        data-test-id={dataTestId}
        design={design}
        id={id}
        as={as}
        {...{ htmlFor, color }}
      >
        {children}
      </Lgd>
    )
  }
  return (
    <P2 {...commonProps} {...{ bold }}>
      {children}
    </P2>
  )
}

Text.propTypes = {
  /** If the text is a heading, specifying a number as a string links to that level of heading, ie H1 through H4. */
  heading: PropTypes.oneOf(['1', '2', '3', '4', '', true]),
  /** If the text is used for a paragraph, paragraph='2' is the default state of the Text component without either this prop or heading set, 1 is slightly larger, 3 is slightly smaller. */
  paragraph: PropTypes.oneOf(['1', '2', '3', '', true]),
  /** If the text is a field label, only to be used in conjunction with custom form input elements. */
  label: PropTypes.oneOf(['regular', 'large', true]),
  /** If the text is a fieldset legend, only to be used in conjunction with fieldset form elements. */
  legend: PropTypes.oneOf(['regular', 'large', true]),
  /** Sets the data-test-id attribute to place on the html element. */
  dataTestId: PropTypes.string,
  /** Sets the id attribute to place on the html element. */
  id: PropTypes.string,
  /** Sets the for attribute on the html element if the Text is a label. */
  htmlFor: PropTypes.string,
  /** The colour to use for the text. */
  color: PropTypes.oneOf(['granite', 'deep-current']),
  /** Whether the text is on a dark background, setting this flag to true will make the text white. */
  invert: PropTypes.bool,
  /** Sets the text component to 50% opacity. */
  fade: PropTypes.bool,
  /** Remove all CSS margins from text. */
  zeroMargin: PropTypes.bool,
  /** Applies Horizon Brand and Design System styling, other values deprecate however required for consistency */
  design: PropTypes.oneOf(['', 'horizon', 'internal', 'external']),
  /** Polymorhic prop to change the html tag. Keeps all the styling applied to a component but just switches out what is ultimately rendered, eg `as='h1'`  */
  as: PropTypes.string
}

// Exporting H4-6 only for backward compatibility.
export {
  Text as default,
  H1,
  H2,
  H3,
  H4,
  H3 as H5,
  H3 as H6,
  P1,
  P2,
  P3,
  Lbl,
  Lgd
}
