import React from 'react'
import PropTypes from 'prop-types'

import PreviousPageIcon from '@anz/icon/dist/filled/arrows-and-symbols/arrow-left'
import NextPageIcon from '@anz/icon/dist/filled/arrows-and-symbols/arrow-right'

import { PaginationWrapper, PaginationButton } from './styles'

const Pagination = ({ id, results, currentPage, updatePage, perPage }) => {
  const pages = Math.ceil(results.length / perPage)

  const pagesArray = []
  for (let i = 1; i <= pages; i++) {
    pagesArray.push(i)
  }

  return (
    <PaginationWrapper data-test-id={id}>
      <PaginationButton
        type='button'
        onClick={() => updatePage(currentPage - 1)}
        disabled={currentPage === 1}
        data-test-id={`${id}_prev-page`}
        aria-label='Go to previous page'
      >
        <PreviousPageIcon />
      </PaginationButton>
      {pagesArray.map(i => (
        <PaginationButton
          key={i}
          type='button'
          onClick={() => updatePage(i)}
          disabled={i === currentPage}
          data-test-id={`${id}_page-${i}`}
          aria-label={`Go to page ${i}`}
        >
          {i}
        </PaginationButton>
      ))}
      <PaginationButton
        type='button'
        onClick={() => updatePage(currentPage + 1)}
        disabled={currentPage === pages}
        data-test-id={`${id}_next-page`}
        aria-label='Go to next page'
      >
        <NextPageIcon />
      </PaginationButton>
    </PaginationWrapper>
  )
}

Pagination.propTypes = {
  /** The id attribute for the DOM. */
  id: PropTypes.string.isRequired,
  /** The results array to paginate over, only required for utility purposes of determining length. */
  results: PropTypes.arrayOf(PropTypes.shape).isRequired,
  /** The current page to display in base 1 numbering format. */
  currentPage: PropTypes.number,
  /** A function passed in to update the section of the results shown by clicking on an action within pagination. */
  updatePage: PropTypes.func.isRequired,
  /** How many results are shown per page. */
  perPage: PropTypes.number
}

Pagination.defaultProps = {
  results: [],
  currentPage: 1,
  perPage: 10
}

export default Pagination
