import React from 'react'
import PropTypes from 'prop-types'
import { AnzGrid, AnzCol } from '@anz/grid'
import { HeaderWrapper, Logo, HeaderRow } from './styles'

import logo from './anz-logo.svg'

const Header = props => (
  <HeaderWrapper data-test-id='header'>
    <AnzGrid fluid maxWidth={props.maxWidth}>
      <HeaderRow>
        <AnzCol xs={6} sm={4} md={3} lg={2}>
          {props.link ? (
            <a
              href={props.link}
              target={props.target ? props.target : '_blank'}
              rel='noopener'
            >
              <Logo data-test-id='header-logo' alt='ANZ Logo' src={logo} />
            </a>
          ) : (
            <Logo data-test-id='header-logo' alt='ANZ Logo' src={logo} />
          )}
        </AnzCol>
        {props.children}
      </HeaderRow>
    </AnzGrid>
  </HeaderWrapper>
)

Header.propTypes = {
  /** Any elements required inside the header. */
  children: PropTypes.node,
  /** A URL if a link is required on the ANZ icon. */
  link: PropTypes.string,
  /** If there is a link on the icon, the target that the link is opened in. */
  target: PropTypes.oneOf(['_blank', '_parent', '_self', '_top']),
  /** The maximum width of the underlying grid within the header, offsets the icon and any content while keeping the background spanning 100% of its container. */
  maxWidth: PropTypes.oneOf([
    '320',
    '480',
    '768',
    '980',
    '1164',
    '1366',
    'auto'
  ])
}

export default Header
