import styled, { css } from 'styled-components'

const enhanceIcon = Component => styled(Component)`
  transform: translate3d(0, 0.125em, 0);
  margin: -0.125em 0 0;

  ${({ color }) =>
    color &&
    css`
      color: ${color};
    `}

  ${({ size }) =>
    size &&
    css`
      font-size: ${size};
    `}

  ${({ angle }) =>
    angle &&
    css`
      transform: rotate(${angle}deg);
    `}
`

export default enhanceIcon
