import React from 'react'
import PropTypes from 'prop-types'

import { colors } from '@anz/styles-global'

import {
  BubblesRoot,
  SvgContainer,
  FullSvg,
  SvgText,
  LabelText,
  StatusIndicator,
  StatusContainer,
  StatusText,
  Gauge
} from './styles'

const ArcCircle = ({
  bubbleSize,
  percentage,
  bubbleText,
  bgColor,
  bubbleTextColor,
  outLineColor
}) => {
  const fontSize = bubbleSize / 2.5
  const strokeWidth = 4
  const outerRadius = bubbleSize / 2 - strokeWidth / 2
  const innerRadius = outerRadius - strokeWidth * 2
  const cx = bubbleSize / 2
  const cy = bubbleSize / 2
  const fontY = cy + fontSize / 3
  const circumference = 2 * Math.PI * outerRadius
  const completedCircumference =
    circumference - (circumference * percentage) / 100
  return (
    <FullSvg>
      <circle
        r={percentage !== undefined ? innerRadius : outerRadius}
        cx={cx}
        cy={cy}
        fill={bgColor}
      />
      {percentage !== undefined && (
        <circle
          r={outerRadius}
          cx={cx}
          cy={cy}
          stroke={outLineColor}
          strokeWidth={strokeWidth}
          fill='none'
        />
      )}
      {percentage !== undefined && (
        <Gauge
          r={outerRadius}
          cx={cx}
          cy={cy}
          stroke={bgColor}
          strokeWidth={strokeWidth}
          completedCircumference={completedCircumference}
          circumference={circumference}
          fill='none'
          transform={`rotate(-90 ${cx} ${cy})`}
        />
      )}
      <SvgText
        textAnchor='middle'
        fontSize={fontSize}
        x={cx}
        y={fontY}
        fill={bubbleTextColor}
      >
        {bubbleText}
      </SvgText>
    </FullSvg>
  )
}

const Status = ({ children, statusType, statusFontSize }) =>
  children ? (
    <StatusContainer>
      {statusType && (
        <StatusIndicator
          statusType={statusType}
          statusFontSize={statusFontSize}
        />
      )}
      <StatusText statusFontSize={statusFontSize}>{children}</StatusText>
    </StatusContainer>
  ) : null

const constructAriaLabel = (label, bubbleText, statusText) => {
  let ariaLabel = `${label}. `
  ariaLabel += bubbleText ? `Value ${bubbleText}` : ''
  ariaLabel += bubbleText && statusText ? 'and ' : ''
  ariaLabel += statusText ? `status ${statusText}` : ''
  return ariaLabel
}

const Bubble = ({
  label,
  id,
  labelColor,
  labelPosition,
  statusText,
  statusType,
  wrapperSize,
  bubbleSize,
  bubbleText,
  labelFontSize,
  ...rest
}) => (
  <BubblesRoot
    wrapperSize={wrapperSize}
    bubbleSize={bubbleSize}
    id={id}
    data-test-id={id}
    aria-label={constructAriaLabel(label, bubbleText, statusText)}
  >
    {label && labelPosition === 'top' && (
      <LabelText labelColor={labelColor} labelFontSize={labelFontSize}>
        {label}
      </LabelText>
    )}
    <SvgContainer bubbleSize={bubbleSize}>
      <ArcCircle bubbleSize={bubbleSize} bubbleText={bubbleText} {...rest} />
    </SvgContainer>
    {label && labelPosition === 'bottom' && (
      <LabelText labelColor={labelColor} labelFontSize={labelFontSize}>
        {label}
      </LabelText>
    )}
    <Status statusFontSize={labelFontSize - 1} statusType={statusType}>
      {statusText}
    </Status>
  </BubblesRoot>
)

Bubble.propTypes = {
  /** Unique identifier for the component */
  id: PropTypes.string.isRequired,
  /** Main text outside of the bubble. */
  label: PropTypes.string,
  /** Whether the label is positioned above or below the bubble. */
  labelPosition: PropTypes.oneOf(['top', 'bottom']),
  /** Colour of the label. */
  labelColor: PropTypes.string,
  /** Text describing the present status, positioned at the bottom of the bubble. */
  statusText: PropTypes.string,
  /** The status given to the bubble. */
  statusType: PropTypes.oneOf(['success', 'information', 'warning', 'error']),
  /** Size of the bubble itself. */
  bubbleSize: PropTypes.number,
  /** Wrapper size incase the label should extend past the bubble. */
  wrapperSize: PropTypes.number,
  /** Font size for the label in pixels. */
  labelFontSize: PropTypes.number,
  /** The amount of the gauge surrounding the bubble that is filled. */
  percentage: PropTypes.number,
  /** Text to display within the bubble, generally matches the percentage. */
  bubbleText: PropTypes.string,
  /** Colour of the text within the bubble. */
  bubbleTextColor: PropTypes.string,
  /** Colour of the background of the bubble and surrounding gauge. */
  bgColor: PropTypes.string,
  /** Colour of the filled part of the gauge. */
  outLineColor: PropTypes.string
}

Bubble.defaultProps = {
  bubbleSize: 100,
  labelFontSize: 14,
  bubbleText: '',
  bgColor: colors.greyscale.smoke,
  bubbleTextColor: colors.core.textGrey,
  outLineColor: colors.secondary.sky
}

export default Bubble
