import styled, { css } from 'styled-components'
import styleVars from '@anz/styles-global'

export const Button = styled.button`
  font-family: 'Myriad Pro', sans-serif;
  font-weight: 600;
  border-radius: 3px;
  cursor: pointer;
  text-decoration: none;
  font-size: 16px;
  letter-spacing: -0.3px;
  line-height: 42px;
  padding: 0 16px;
  user-select: none;
  color: ${styleVars.color.greyscale.white};
  background-color: ${styleVars.color.core.oceanBlue};
  border: 1px solid ${styleVars.color.greyscale.white};
  transition: background-color 0.2s, border-color 0.2s, color 0.2s, box-shadow 0.2s;
  outline: none;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;

  display: flex;
  justify-content: center;

  &:hover,
  &:focus {
    background-color: ${styleVars.color.variants.oceanBlue.tint};
  }

  &:active {
    background-color: ${styleVars.color.variants.oceanBlue.shade};
  }

  &[disabled], &[aria-disabled='true'] {
    opacity: 0.4;
    cursor: not-allowed;

    &:hover,
    &:focus {
      background-color: ${styleVars.color.core.oceanBlue};
    }
  }

  ${props =>
    props.fullWidth &&
    css`
      width: 100%;
    `}
  
  ${props =>
    props.size === 'extra-small' &&
    css`
      line-height: 28px;
    `}
  
  ${props =>
    props.size === 'small' &&
    css`
      line-height: 36px;
    `}

  ${props =>
    props.size === 'large' &&
    css`
      font-size: 20px;
      line-height: 58px;
    `}

  ${props =>
    props.appearance === 'action' &&
    css`
      background-color: ${styleVars.color.system.success};

      &:hover,
      &:focus {
        background-color: ${styleVars.color.variants.success.tint};
      }

      &:active {
        background-color: ${styleVars.color.variants.success.shade};
      }

      &[disabled] {
        &:hover,
        &:focus {
          background-color: ${styleVars.color.system.success};
        }
      }

      ${props =>
        props.variant === 'ghost' &&
        css`
          background-color: none;
          border-color: ${styleVars.color.system.success};
        `}
    `}

  ${props =>
    props.appearance === 'destructive' &&
    css`
      background-color: ${styleVars.color.system.error};

      &:hover,
      &:focus {
        background-color: ${styleVars.color.variants.error.tint};
      }

      &:active {
        background-color: ${styleVars.color.variants.error.shade};
      }

      &[disabled] {
        &:hover,
        &:focus {
          background-color: ${styleVars.color.system.error};
        }
      }
    `}

  ${props =>
    props.appearance === 'card' &&
    css`
      background-color: white;
      border-color: #ccc;
      color: #999;

      &:hover,
      &:focus {
        background-color: whitesmoke;
      }

      &:active {
        background-color: whitesmoke;
      }

      &[disabled] {
        &:hover,
        &:focus {
          background-color: whitesmoke;
        }
      }
    `}

  ${props =>
    (props.variant === 'ghost' || props.appearance === 'ghost') &&
    css`
      background-color: transparent;
      border-color: ${styleVars.color.core.linkBlue};
      color: ${styleVars.color.core.linkBlue};

      &:hover,
      &:focus {
        background-color: ${styleVars.color.variants.oceanBlue.tint};
        color: ${styleVars.color.greyscale.white};
        border-color: ${styleVars.color.greyscale.white};
      }

      &:active {
        background-color: ${styleVars.color.variants.oceanBlue.shade};
      }

      &[disabled] {
        &:hover,
        &:focus {
          background-color: ${styleVars.color.greyscale.white};
          color: ${styleVars.color.core.linkBlue};
          border-color: ${styleVars.color.core.linkBlue};
        }
      }

      ${props =>
        props.appearance === 'action' &&
        css`
          border-color: ${styleVars.color.system.success};
          color: ${styleVars.color.system.success};

          &:hover,
          &:focus {
            background-color: ${styleVars.color.variants.success.tint};
          }

          &:active {
            background-color: ${styleVars.color.variants.success.shade};
          }
        `}

      ${props =>
        props.appearance === 'destructive' &&
        css`
          border-color: ${styleVars.color.system.error};
          color: ${styleVars.color.system.error};

          &:hover,
          &:focus {
            background-color: ${styleVars.color.variants.error.tint};
          }

          &:active {
            background-color: ${styleVars.color.variants.error.shade};
          }
        `}
    `}

  ${props =>
    props.appearance === 'text' &&
    css`
      padding: 0;
      text-align: left;
      color: ${styleVars.color.core.oceanBlue};
      background: transparent;
      border: 0;
      font-weight: 400;

      &:hover,
      &:focus {
        background: transparent;
        text-decoration: underline;

        &[disabled] {
          background: transparent;
          text-decoration: none;
        }
      }
    `}

  ${props =>
    props.icon &&
    props.iconPlacement === 'left' &&
    css`
      & svg {
        margin-right: 8px;
      }
    `}

  ${props =>
    props.icon &&
    props.iconPlacement === 'right' &&
    css`
      & svg {
        margin-left: 8px;
      }
    `}

  ${props =>
    props.isContainer &&
    css`
      position: relative;
      overflow: hidden;
    `}

  ${props =>
    props.design === 'horizon' &&
    css`
      border-radius: 4px;
      line-height: 24px;
      padding: 11px 23px;
      border-color: transparent;

      &[disabled], &[aria-disabled='true'] {
        opacity: 1;
        background-color: ${styleVars.color.greyscale.cloud};
        color: ${styleVars.color.core.textGrey};
        
        &:hover,
        &:focus {
          background-color: ${styleVars.color.greyscale.cloud};
          text-decoration: none;
        }
      }
      
      ${props =>
        (props.appearance === 'primary' || !props.appearance) &&
        css`
          background-color: ${styleVars.color.core.linkBlue};
          &:hover,
          &:focus {
            background-color: ${styleVars.color.horizonVariants.linkBlue.hover};
          }
          &:active {
            background-color: ${styleVars.color.core.deepCurrent};
          }
          ${props =>
            props.active &&
            css`
              background-color: ${styleVars.color.core.deepCurrent};
              &[disabled],
              &[aria-disabled='true'] {
                background-color: ${styleVars.color.core.deepCurrent};
                color: ${styleVars.color.greyscale.white};
                &:hover,
                &:focus {
                  background-color: ${styleVars.color.core.deepCurrent};
                  color: ${styleVars.color.greyscale.white};
                }
              }
            `}
        `}
      
      &:hover, &:focus {
        text-decoration: underline;
      }

      &:focus {
        box-shadow: 0 0 0 3px ${
          styleVars.color.horizonVariants.linkBlue.superLight
        };
        text-decoration: underline;
      }

      &:active {
        text-decoration: none;
      }

      ${props =>
        props.appearance === 'action' &&
        css`
          background-color: ${styleVars.color.system.systemGreen};
          &:hover,
          &:focus {
            background-color: ${styleVars.color.horizonVariants.systemGreen
              .hover};
          }

          &:focus {
            box-shadow: 0 0 0 3px
              ${styleVars.color.horizonVariants.systemGreen.superLight};
          }

          &:active {
            background-color: ${styleVars.color.horizonVariants.systemGreen
              .active};
          }

          ${props =>
            props.active &&
            css`
              background-color: ${styleVars.color.horizonVariants.systemGreen
                .active};
              &[disabled],
              &[aria-disabled='true'] {
                background-color: ${styleVars.color.horizonVariants.systemGreen
                  .active};
                color: ${styleVars.color.greyscale.white};
                &:hover,
                &:focus {
                  background-color: ${styleVars.color.horizonVariants
                    .systemGreen.active};
                  color: ${styleVars.color.greyscale.white};
                }
              }
            `}
        `}

      ${props =>
        props.appearance === 'destructive' &&
        css`
          background-color: ${styleVars.color.system.systemRed};
          &:hover,
          &:focus {
            background-color: ${styleVars.color.horizonVariants.systemRed
              .hover};
          }

          &:focus {
            box-shadow: 0 0 0 3px
              ${styleVars.color.horizonVariants.systemRed.superLight};
          }

          &:active {
            background-color: ${styleVars.color.horizonVariants.systemRed
              .active};
          }

          ${props =>
            props.active &&
            css`
              background-color: ${styleVars.color.horizonVariants.systemRed
                .active};
              &[disabled],
              &[aria-disabled='true'] {
                background-color: ${styleVars.color.horizonVariants.systemRed
                  .active};
                color: ${styleVars.color.greyscale.white};
                &:hover,
                &:focus {
                  background-color: ${styleVars.color.horizonVariants.systemRed
                    .active};
                  color: ${styleVars.color.greyscale.white};
                }
              }
            `}
        `}

      ${props =>
        props.size === 'extra-small' &&
        css`
          padding: 3px 15px;
          line-height: 20px;
          font-size: 14px;
        `}

      ${props =>
        props.size === 'small' &&
        css`
          padding: 7px 15px;
          line-height: 24px;
        `}

      ${props =>
        props.size === 'large' &&
        css`
          line-height: 28px;
          font-size: 20px;
          padding: 15px 47px;
          border-radius: 8px;
        `}

      ${props =>
        props.appearance === 'text' &&
        css`
          font-weight: 600;
          padding-left: 0;
          padding-right: 0;

          &:hover,
          &:focus {
            color: ${styleVars.color.horizonVariants.linkBlue.hover};
          }

          &:active {
            text-decoration: none;
            color: ${styleVars.color.core.deepCurrent};
          }

          &[disabled],
          &[aria-disabled='true'] {
            background-color: transparent;
            &:hover,
            &:focus {
              background-color: transparent;
              color: ${styleVars.color.core.textGrey};
            }
          }
        `}

      ${props =>
        props.appearance === 'card' &&
        css`
          background-color: white;
          border-color: #ccc;
          color: #999;
          padding: 0 16px;
          line-height: 42px;
        `}

      ${props =>
        (props.variant === 'ghost' || props.appearance === 'ghost') &&
        css`
          color: ${styleVars.color.core.linkBlue};
          background: transparent;
          border-color: ${styleVars.color.core.linkBlue};

          &:hover {
            background-color: transparent;
            color: ${styleVars.color.horizonVariants.linkBlue.hover};
            border-color: ${styleVars.color.horizonVariants.linkBlue.hover};
          }

          &:focus {
            background-color: transparent;
            color: ${styleVars.color.core.linkBlue};
            border-color: ${styleVars.color.horizonVariants.linkBlue.hover};
            box-shadow: 0 0 0 3px
              ${styleVars.color.horizonVariants.linkBlue.superLight};
          }

          &:active {
            color: ${styleVars.color.core.deepCurrent};
            border-color: ${styleVars.color.core.deepCurrent};
            background-color: transparent;
          }

          &[disabled],
          &[aria-disabled='true'] {
            background: transparent;
            border-color: ${styleVars.color.greyscale.cloud};
            &:hover,
            &:focus {
              background: transparent;
              border-color: ${styleVars.color.greyscale.cloud};
              color: ${styleVars.color.core.textGrey};
            }
          }

          ${props =>
            props.size === 'large' &&
            css`
              box-shadow: inset 0 0 0 1px ${styleVars.color.core.linkBlue};
              &:focus {
                box-shadow: 0 0 0 3px
                    ${styleVars.color.horizonVariants.linkBlue.superLight},
                  inset 0 0 0 1px ${styleVars.color.core.linkBlue};
              }
              &[disabled],
              &[aria-disabled='true'] {
                box-shadow: inset 0 0 0 1px ${styleVars.color.greyscale.cloud};
              }
            `}
        `}
  `}
`

const alignmentOptions = {
  center: 'center',
  start: 'flex-start',
  end: 'flex-end'
}

export const ButtonGroup = styled.div`
  display: flex;
  gap: 16px;
  flex-direction: row;
  align-items: ${({ alignment }) => alignmentOptions[alignment || 'center']};

  ${props =>
    props.vertical &&
    css`
      text-align: center;
      gap: 8px;
      flex-direction: column;
    `}
`

export const ContentWrapper = styled.span`
  display: inline-block;

  ${({ appearance }) =>
    appearance === 'card' &&
    css`
      text-align: left;
      line-height: 1.5em;
      padding: 8px 0;
      flex-grow: 1;
    `}

  ${({ appearance }) =>
    appearance === 'icon' &&
    css`
      flex-grow: 0;
      height: 100%;
      z-index: 1;
    `}
`
