import React from 'react'
import PropTypes from 'prop-types'

import { Wrapper } from './styles'

const Section = ({ id, children, variant, highlight }) => {
  const wrapperProps = { id, variant, highlight }
  return (
    <Wrapper data-test-id={id} {...wrapperProps}>
      {children}
    </Wrapper>
  )
}

Section.propTypes = {
  /** The id attribute for the DOM. */
  id: PropTypes.string,
  /** Section content is specified by its children. */
  children: PropTypes.node,
  /** Whether the section is based on the internal or external UI kit. */
  variant: PropTypes.oneOf(['internal', 'external']),
  /** The colour of the top stripe on the section if it exists. */
  highlight: PropTypes.oneOf(['ocean-blue', 'seagrass'])
}

export default Section
