const tint = (colour, amount = 20) =>
  tinycolor.mix(colour, '#FFFFFF', amount).toString()
const shade = (colour, amount = 20) =>
  tinycolor.mix(colour, '#000000', amount).toString()
const alpha = (colour, amount = 0.4) =>
  tinycolor(colour)
    .setAlpha(amount)
    .toRgbString()

