const GlobalStyle = createGlobalStyle`

  @font-face {
    font-family: 'Myriad Pro';
    font-display: swap;
    src: url(${myriadProLightItalicWoff2}) format('woff2'), url(${myriadProLightItalicWoff}) format('woff');
    font-style: italic;
    font-weight: 300;
  }

  @font-face {
    font-family: 'Myriad Pro';
    font-display: swap;
    src: url(${myriadProNormalItalicWoff2}) format('woff2'), url(${myriadProNormalItalicWoff}) format('woff');
    font-style: italic;
    font-weight: 400;
  }

  @font-face {
    font-family: 'Myriad Pro';
    font-display: swap;
    src: url(${myriadProBoldItalicWoff2}) format('woff2'), url(${myriadProBoldItalicWoff}) format('woff');
    font-style: italic;
    font-weight: 600;
  }

  @font-face {
    font-family: 'Myriad Pro';
    font-display: swap;
    src: url(${myriadProLightWoff2}) format('woff2'), url(${myriadProLightWoff}) format('woff');
    font-style: normal;
    font-weight: 300;
  }

  @font-face {
    font-family: 'Myriad Pro';
    font-display: swap;
    src: url(${myriadProNormalWoff2}) format('woff2'), url(${myriadProNormalWoff}) format('woff');
    font-style: normal;
    font-weight: 400;
  }

  @font-face {
    font-family: 'Myriad Pro';
    font-display: swap;
    src: url(${myriadProBoldWoff2}) format('woff2'), url(${myriadProBoldWoff}) format('woff');
    font-style: normal;
    font-weight: 600;
  }

  ${styledNormalize}
  
  *, ::after, ::before {
    box-sizing: border-box;
  }

  body {
    margin: 0;
    font-family: 'Myriad Pro', sans-serif;
    font-size: 16px;
    line-height: 1.5em;
    letter-spacing: -0.3px;
    color: ${styleVars.color.darkGrey};
    /* Font variant */
    font-variant-ligatures        : none;
    -webkit-font-variant-ligatures: none;
    /* Smoothing */
    text-rendering                : optimizeLegibility;
    -moz-osx-font-smoothing       : grayscale;
    -webkit-font-smoothing        : antialiased;
    min-width: 320px;
  }

  a {
    color: ${styleVars.color.htmlLink};
    text-decoration: none;

    &:hover,
    &:focus {
      text-decoration: underline;
    }
  }
`

export { styleVars as default, colors, GlobalStyle }
