import React from 'react'
import PropTypes from 'prop-types'
import { Button, ButtonGroup, ContentWrapper } from './styles'

const AnzButton = ({
  appearance,
  size,
  disabled,
  fullWidth,
  type,
  id,
  autoFocus,
  name,
  value,
  icon,
  iconPlacement,
  children,
  onClick,
  isContainer,
  variant,
  onBlur,
  onFocus,
  ariaExpanded,
  ariaDisabled,
  ariaLabel,
  active,
  design
}) => {
  const buttonProps = {}
  if (appearance) buttonProps.appearance = appearance
  if (active) buttonProps.active = active
  if (size) buttonProps.size = size
  if (disabled) buttonProps.disabled = disabled
  if (fullWidth) buttonProps.fullWidth = fullWidth
  buttonProps.type = type || 'button'
  if (id) {
    buttonProps.id = id
    buttonProps['data-test-id'] = `${buttonProps.id}_button`
  }
  if (autoFocus) buttonProps.autoFocus = autoFocus
  if (name) buttonProps.name = name
  if (value) buttonProps.value = value
  if (onClick && !ariaDisabled) buttonProps.onClick = onClick
  if (onFocus) buttonProps.onFocus = onFocus
  if (onBlur) buttonProps.onBlur = onBlur
  if (icon) buttonProps.icon = icon
  buttonProps.iconPlacement = iconPlacement || 'left'
  if (isContainer) buttonProps.isContainer = isContainer
  if (variant) buttonProps.variant = variant
  if (design) buttonProps.design = design

  const IconChild = <ContentWrapper appearance='icon'>{icon}</ContentWrapper>

  return (
    <Button
      {...buttonProps}
      aria-expanded={ariaExpanded}
      aria-disabled={ariaDisabled}
      aria-label={ariaLabel}
    >
      {buttonProps.iconPlacement === 'left' && icon && IconChild}
      <ContentWrapper appearance={appearance}>{children}</ContentWrapper>
      {buttonProps.iconPlacement === 'right' && icon && IconChild}
    </Button>
  )
}

AnzButton.propTypes = {
  /**
   * primary: Ocean blue, white text (default look and feel).
   * action: Green, white text.
   * destructive: Red, white text.
   * ghost: White, ocean blue text and border.
   * card: Light grey border and hover.
   * Plain text.
   */
  appearance: PropTypes.oneOf([
    'primary',
    'action',
    'destructive',
    'ghost',
    'text',
    'card'
  ]),
  /** TODO: API Incomplete. Ghost is still in appearance for backwards compatibility.
   *  TODO: Current UI Kit indicates size & ghost as different variants, uncertain this maps intelligently to code needs.
   */
  variant: PropTypes.oneOf(['normal', 'ghost']),
  /**
   * Utilise look/feel/user interaction from alternate design platform
   * horizon: utilise design from 'Horizon' brand and design system.
   * */
  design: PropTypes.oneOf(['', 'horizon']),
  /** large: Body text size 2 (16px), default is size 1 (14px), small decreases padding to fit within tables etc. */
  size: PropTypes.oneOf(['extra-small', 'small', 'medium', 'large']),
  /** Whether the button is in a disabled state. */
  disabled: PropTypes.bool,
  /** Applies `width: 100%;` to the button. */
  fullWidth: PropTypes.bool,
  /** Optional icon */
  icon: PropTypes.oneOfType([PropTypes.string, PropTypes.element]),
  /** Icon placement */
  iconPlacement: PropTypes.oneOf(['', 'left', 'right']),
  /** Special case for when this button contains complex children */
  /** TODO: only needed due to current structure disallowing access to core styled component, "builder" pattern will make this redundant */
  isContainer: PropTypes.bool,
  /** When set to true will invoke the button 'active' state */
  active: PropTypes.bool,
  /** The default behavior of the button, value set to `button` by default **/
  type: PropTypes.oneOf(['', 'submit', 'reset', 'button'])
}

/** Component usage: <Button /> */
export { AnzButton as default, ButtonGroup }
