import React from 'react'
import PropTypes from 'prop-types'
import AnzIcon from './filled/banking-and-processes/action-menu'

const IconCmpt = props => <AnzIcon {...props} />

const IconPropTypes = {
  /** Colour applied to the icon. */
  color: PropTypes.string,
  /** Size of the icon, can be any valid css size. */
  size: PropTypes.string,
  /** Rotate the icon by this angle clockwise. */
  angle: PropTypes.string,
  /** The id attribute for the DOM. */
  id: PropTypes.string
}

IconCmpt.propTypes = IconPropTypes

/** Component usage: <Icon /> */
export { IconPropTypes, IconCmpt as default }
